.FooterLayout{width:100%;height:320px;background-color:#303740;display:flex;flex-direction:column;align-items:center}.FooterLayoutStart{margin-top:20px;width:70%;height:50%;display:flex;color:#f5f5f5;justify-content:space-around}.FooterLayoutEnd{width:70%;height:50%;color:#f5f5f5}.Service h3,.Terms h3,.Ask h3,.CustomerCenter h3,.Logo h3{font-family:NotoSans-Bold;margin:10px 5px 15px 15px}.Service h4,.Terms h4,.Ask h4,.CustomerCenter h4,.Logo h4{font-family:NotoSans-Regular;font-size:14px;margin:5px 15px}.Service p,.Terms p,.Ask p,.CustomerCenter p,.Logo p{font-family:NotoSans-Regular;margin:5px 15px;font-size:12px}.Service img,.Terms img,.Ask img,.CustomerCenter img,.Logo img{margin-top:25px}.CompanyName{font-family:NotoSans-Bold;margin:30px 5px 15px 15px;font-size:12px}.CompanyDesc{font-family:NotoSans-Regular;margin:5px 15px;font-size:12px}
.FirstSlide{background-color:#27d68d;display:inline-flex !important;height:18.75rem}.FirstSlide .cover-left{width:40%;height:100%}.FirstSlide .cover-left .comment-wrapper{margin-left:90px;margin-top:10px;width:400px;height:150px;position:relative;top:44%;transform:translateY(-50%);left:30%}.FirstSlide .cover-left .comment-wrapper .comment-title{font-size:35px;color:#fff;font-family:NotoSans-Bold}.FirstSlide .cover-left .comment-wrapper .comment-sub{font-size:15px;color:#fff;font-family:NotoSans-Regular}.FirstSlide .cover-right{width:60%;height:100%}.FirstSlide img{width:90%;height:100%;float:right}.SecondSlide{background-color:#8aeef2;display:inline-flex !important;flex-direction:row;height:18.75rem}.SecondSlide .cover-left{width:40%;height:100%}.SecondSlide .cover-left .comment-wrapper{margin-left:100px;width:400px;height:150px;position:relative;top:44%;transform:translateY(-50%);left:30%}.SecondSlide .cover-left .comment-wrapper .comment-title{font-size:38px;font-family:NotoSans-Bold;color:#000}.SecondSlide .cover-left .comment-wrapper .comment-sub{font-size:15px;color:#000;font-family:NotoSans-Regular}.SecondSlide .cover-right{width:60%;height:100%}.SecondSlide img{width:85%;height:100%;float:right;margin-top:20px;margin-right:120px}.ThirdSlide{background-color:#fbfbf3;display:inline-flex !important;flex-direction:row;height:18.75rem}.ThirdSlide .cover-left{width:40%;height:100%}.ThirdSlide .cover-left .comment-wrapper{margin-left:100px;width:400px;height:150px;position:relative;top:44%;transform:translateY(-50%);left:30%}.ThirdSlide .cover-left .comment-wrapper .comment-title{font-size:35px;font-family:NotoSans-Bold;color:#000}.ThirdSlide .cover-left .comment-wrapper .comment-sub{font-size:15px;font-family:NotoSans-Regular;color:#000}.ThirdSlide .cover-right{width:60%;height:100%}.ThirdSlide img{width:90%;height:100%;float:right}
.App{height:100%;font-family:NotoSans-Regular}.App .body-layout{width:100%;height:100%;min-width:37.5rem;display:inline-block;margin:0 auto}.App .input-margin-bottom{margin-bottom:10px}.App button:focus{box-shadow:none !important}@font-face{font-family:NotoSans-Black;src:url(/static/media/NotoSansKR-Black.57ee16cd.otf)}@font-face{font-family:NotoSans-Bold;src:url(/static/media/NotoSansKR-Bold.29abf35d.otf)}@font-face{font-family:NotoSans-Medium;src:url(/static/media/NotoSansKR-Medium.72ba4555.otf)}@font-face{font-family:NotoSans-Regular;src:url(/static/media/NotoSansKR-Regular.457762ea.otf)}.App-logo{height:40vmin;pointer-events:none}@media(prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}
